import { Injectable } from '@angular/core';
import { NavigationEnd, Router, Event as RouterEvent } from '@angular/router';
import { BehaviorSubject, filter } from 'rxjs';
import { MenuNavegacion } from '../models/configuracion.model';

@Injectable({
  providedIn: 'root',
})
export class HeaderService {

  public urlActual: string = ''
  private readonly actualizarHeaderSubject = new BehaviorSubject<boolean>(false)
  actualizarHeader$ = this.actualizarHeaderSubject.asObservable()

  constructor(private readonly router: Router) {

    this.router.events
      .pipe(
        filter(
          (event: RouterEvent): event is NavigationEnd => event instanceof NavigationEnd
        )
      ).subscribe((event: NavigationEnd) => {
        this.urlActual = event.urlAfterRedirects;
      });
  }

  public setActualizarHeader(valor: boolean): void {
    this.actualizarHeaderSubject.next(valor)
  }

  public getUrlActual(prefijoURL: string): string {
    return this.urlActual.replace(prefijoURL, '').split('?')[0];
  }

  public getMenuActual(
    menu: MenuNavegacion[],
    prefijoURL: string
  ): MenuNavegacion | undefined {
    const url = this.getUrlActual(prefijoURL);
    return menu.find( (item) => url === item.url || url.startsWith(item.url + '/')     );
  }

}
