import { CommonModule, Location, NgClass, TitleCasePipe } from '@angular/common';
import { Component, EventEmitter, Input, OnInit, Output, inject } from '@angular/core';
import { RouterLink, RouterLinkActive } from '@angular/router';
import { MenuModule } from 'primeng/menu';
import { Alertas, Aplicaciones, MenuNavegacion, MenuNavegacionBotones, TipoMenuNavegacion, Titulos, Usuario } from '../models/configuracion.model';
import { HeaderService } from '../services/header.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'ngx-cfng-core-layout-header',
  standalone: true,
  imports: [
    MenuModule,
    NgClass,
    RouterLink,
    RouterLinkActive,
    CommonModule,
    TitleCasePipe
  ],
  templateUrl: './header.component.html',
  styleUrl: './header.component.scss',
})
export class NgxCfngCoreLayoutHeaderComponent implements OnInit {
  @Input({ required: true })
  public baseAssetsUrl: string = '';

  @Input()
  public aplicaciones: Aplicaciones[] = [];

  @Input()
  public alertas?: Alertas[] = [];

  @Input()
  public usuario: Usuario = {} as Usuario;

  @Input()
  public titulos: Titulos = {} as Titulos;

  @Input()
  public menuNavegacion: MenuNavegacion[] = [];

  @Input()
  public tipoMenu: TipoMenuNavegacion = TipoMenuNavegacion.Sidebar;

  @Input()
  public menuNavegacionBotones: MenuNavegacionBotones[] = [];

  @Input()
  public prefijoURL: string = '/app/'; //'/layout/layout/efe/' /app/

  @Output()
  public menuNavegacionSeleccionado = new EventEmitter<MenuNavegacion>();

  protected mostrarMasOpciones: boolean = false;
  protected mostrarMenuNavegacionMovil: boolean = false;
  protected version = '0.0.1';
  //private sessionMenuId = 'mpeMnPr';//Usado para almacenar el menu seleccionado
  protected menuNavegacionSeleccionadoId: string | null = null;
  protected TipoMenuNavegacion = TipoMenuNavegacion;
  protected location: Location = inject(Location);
  private readonly headerService: HeaderService = inject(HeaderService);
  private menuDropdownActivo: MenuNavegacion | null = null;
  private subscription!: Subscription

  ngOnInit(): void {
    this.cargarMenuInicial()
    this.subscription = this.headerService.actualizarHeader$.subscribe(() => {
      this.cargarMenuInicial()
    })
  }

  ngOnDestroy(): void {
    if (this.subscription) {
      this.subscription.unsubscribe()
    }
  }

  private cargarMenuInicial(): void {
    const menu = this.headerService.getMenuActual(this.menuNavegacion, this.prefijoURL);
    if (menu) {
      this.sidebarMenuNavegacionSeleccionar(menu);
    }
  }

  protected eventoMenuNavegacionMovilMostrar() {
    this.mostrarMenuNavegacionMovil = !this.mostrarMenuNavegacionMovil;
  }

  public eventoMenuNavegacionSeleccionar(e: Event, menu: MenuNavegacion): void {
    if (this.tipoMenu === TipoMenuNavegacion.Sidebar) {
      this.sidebarMenuNavegacionSeleccionar(menu);
    } else {
      this.dropdownMenuNavegacionSeleccionar(menu);
    }
  }

  protected eventoSubmenuNavegacion(e: Event, menu: MenuNavegacion) {
    e.stopPropagation();
    menu.extended = false;
  }

  private sidebarMenuNavegacionSeleccionar(menu: MenuNavegacion) {
    this.menuNavegacionSeleccionadoId = menu.code!;
    this.menuNavegacionSeleccionado.emit(menu);
    this.mostrarMenuNavegacionMovil = false;
  }

  private dropdownMenuNavegacionSeleccionar(menu: MenuNavegacion) {
    if (this.menuDropdownActivo !== null && menu.code !== this.menuDropdownActivo.code) {
      this.menuDropdownActivo.extended = false;
    }
    this.menuNavegacionSeleccionadoId = menu.code!;
    this.menuDropdownActivo = menu;
    menu.extended = !menu.extended;
  }

  protected eventoSubmenuDropdown(e: Event, menu: MenuNavegacionBotones) {
    if (menu.click) {
      menu.click(e);
    }
    menu.extended = !menu.extended;
  }

  protected mostrarAlerta(alerta: Alertas) {
    this.ocultarTodasLasAlertas();
    alerta.mostrar = true;
  }

  private ocultarTodasLasAlertas() {
    this.alertas?.forEach((alerta) => {
      alerta.mostrar = false;
    });
  }

  protected ocultarAlerta = (alerta: Alertas) => () => {
    alerta.mostrar = false;
  };
}
